<script>
// @ is an alias to /src
import { mdbCard, mdbCardImage, mdbCardBody, mdbCardTitle, mdbCardText, mdbBtn, mdbBadge } from 'mdbvue';
import NavConnected from '@/components/NavConnected.vue';
import VueSwing from 'vue-swing'


export default {
  name: 'discover',
  components: {
      VueSwing,
      NavConnected,
      mdbCard,
      mdbCardImage,
      mdbCardBody,
      mdbCardTitle,
      mdbCardText,
      mdbBtn,
      mdbBadge

  },
  data(){
            return {
                config: {
                    allowedDirections: [
                        VueSwing.Direction.LEFT,
                        VueSwing.Direction.RIGHT
                    ],
                    // minThrowOutDistance: 10,
                    // maxThrowOutDistance: 300
                },
                numberOfCards: 3,
                cardThrown: 0,
                acceptedPeople: [],
                refusedPeople: [],
                treatedCards: []
            }
        },
        methods: {
            onDragMove(event){
                // console.log('onDragMove')
                // console.log(event);
                // const cards = this.$refs.vueswing.cards
                // console.log(cards);

                // A partir de 0.7 de confidence on estime que la carte est assez loin pour être traitée
                if(event.throwOutConfidence >= 0.3){           
                    if(event.throwDirection == VueSwing.Direction.RIGHT){                      
                        if(this.acceptedPeople.indexOf(event.target.id) === -1){
                            this.$refs.vueswing.stack.getCard(event.target).throwOut(100, 10);

                            this.acceptedPeople.push(event.target.id);
                            this.treatedCards.push(event.target.id);
                            this.cardThrown++;
                        }
                        
                    }
                    else if(event.throwDirection == VueSwing.Direction.LEFT){                       
                        if(this.refusedPeople.indexOf(event.target.id) === -1){
                            this.$refs.vueswing.stack.getCard(event.target).throwOut(-100, 0);
                            this.refusedPeople.push(event.target.id);
                            this.treatedCards.push(event.target.id);
                            this.cardThrown++;
                        }
                    }
                }
                
                // if(event.throwDirection == VueSwing.Direction.INVALID){ 
                //      console.log(event);
                //      event.stopPropagation();
                // }
            },
            refuseCard(cardId){                 
                if(this.refusedPeople.indexOf(this.$refs[cardId].$el.id) === -1){
                    this.$refs.vueswing.stack.getCard(this.$refs[cardId].$el).throwOut(-100, 0);
                    this.refusedPeople.push(this.$refs[cardId].$el.id);
                    this.treatedCards.push(this.$refs[cardId].$el.id);
                    this.cardThrown++;
                }
            },
            acceptCard(cardId){        
                 if(this.acceptedPeople.indexOf(this.$refs[cardId].$el.id) === -1){
                     this.$refs.vueswing.stack.getCard(this.$refs[cardId].$el).throwOut(100, 0);
                    this.acceptedPeople.push(this.$refs[cardId].$el.id);
                    this.treatedCards.push(this.$refs[cardId].$el.id);
                    this.cardThrown++;
                }
            }
        }
}
</script>


