<template>
  <div>
  <div class="logo">
    <img alt="Vue logo" src="../assets/CAIT_Icon_192.png" height="120px">
<!-- <h1 class="caitName">CAIT</h1> -->
    
</div>
<!-- <h3>Création du profil. A faire</h3> -->
<horizontal-stepper 
    class="horizontalStepper"
    :steps="demoSteps" 
    :locale="languageFr"
    @completed-step="completeStep"
    @active-step="isStepActive" 
    @stepper-finished="validateInscription">
</horizontal-stepper>  

<!-- <mdb-btn color="blue-grey" size="sm" class="validerBtn"><router-link to="/discover">Valider</router-link></mdb-btn> -->

  </div>
</template>

<script>
// @ is an alias to /src
import HorizontalStepper from 'vue-stepper';
// import { mdbBtn } from 'mdbvue';

import StepOne from '@/components/StepOne.vue'
import StepTwo from '@/components/StepTwo.vue'
import StepThree from '@/components/StepThree.vue'
import StepFour from '@/components/StepFour.vue'


export default {
  name: 'createProfile',
  components: {
    //   mdbInput,
    //   mdbBtn,
    //   mdbCard,
    //   mdbCardHeader,
    //   mdbCardBody
    HorizontalStepper
    // mdbBtn
  },
  data(){
            return {
                // name: '',
                // email: '',
                // emailConfirm: '',
                // password: ''
                languageFr: 'fr',
                demoSteps: [
                    {
                        icon: 'edit',
                        name: 'first',
                        title: 'Inscription',
                        component: StepOne,
                        completed: false

                    },
                    {
                        icon: 'person',
                        name: 'second',
                        title: 'Qui êtes-vous ?',
                        // subtitle: 'Subtitle sample',
                        component: StepTwo,
                        completed: false
                    },
                    {
                        icon: 'flag',
                        name: 'third',
                        title: 'Dites-nous en plus',
                        // subtitle: 'Subtitle sample',
                        component: StepThree,
                        completed: false
                    },
                    {
                        icon: 'edit_location',
                        name: 'fourth',
                        title: 'Où allez-vous ?',
                        // subtitle: 'Subtitle sample',
                        component: StepFour,
                        completed: false
                    },
                    
                ]
            }
        },
        methods: {
            // inscrire(){
            //     console.log("name = " + this.name);
            // },
            // Executed when @completed-step event is triggered
            completeStep(payload) {
                this.demoSteps.forEach((step) => {
                    if (step.name === payload.name) {
                        step.completed = true;
                    }
                })
            },
            // Executed when @active-step event is triggered
            isStepActive(payload) {
                this.demoSteps.forEach((step) => {
                    if (step.name === payload.name) {
                        if(step.completed === true) {
                            step.completed = false;
                            
                        }
                    }
                })
            },
            // Executed when @stepper-finished event is triggered
            validateInscription() {
                this.$router.push('discover');
                // alert('end')
            }
        }
}
</script>


<style lang="scss">
.caitName{
    font-family:"Classy Beautiful 2";
    display: inline-block;
    position: relative;
    top: 15px;
    color: #364895;
}
.horizontalStepper{
    margin-top: 10px;
}
.validerBtn{
    a{
        color: inherit;
    }
}

.stepper-box .top .steps-wrapper .step .circle i{
    background-color: #364895 !important;
}

.stepper-box .bottom .stepper-button.next{
    background-color: #364895 !important;
}

.stepper-box .content{
    overflow: inherit !important;
}
</style>